:root {
  --matrix-green: #00ff00;
  --matrix-dark: #003300;
  --text-color: #ffffff;
}

body {
  margin: 0;
  padding: 0;
  background-color: black;
  color: var(--text-color);
  font-family: 'Courier New', monospace;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  justify-content: center;
  align-items: center;
  text-align: center;
  user-select: none;
}

#matrix-rain {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}

.container {
  position: relative;
  z-index: 1;
  max-width: 1400px;
  margin: 0 auto;
  padding: 2rem;
}

.content-card {
  background: rgba(0, 0, 0, 0.85);
  padding: 2.5rem;
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(5px);
  margin-bottom: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.hero-image {
  width: 100%;
  max-width: 800px;
  margin: 2rem 0;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 0 20px rgba(0, 255, 0, 0.2);
}

.hero-image img {
  width: 100%;
  height: auto;
  display: block;
  transition: transform 0.3s ease;
}

.hero-image:hover img {
  transform: scale(1.02);
}

.hero-content {
  width: 100%;
  max-width: 1200px;
}

h1 {
  font-size: 3.5rem;
  margin: 0;
  text-transform: uppercase;
  letter-spacing: 0.3rem;
  animation: glow 3s ease-in-out infinite alternate;
  color: rgba(0, 255, 0, 0.8);
  font-weight: 800;
}

h2 {
  font-size: 2rem;
  color: var(--matrix-green);
  margin: 1rem 0;
  font-weight: 700;
}

.subtitle {
  font-size: 1.2rem;
  color: var(--text-color);
  opacity: 0.9;
  margin-bottom: 2rem;
}

.features {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
  margin: 3rem 0;
  width: 100%;
  max-width: 1200px;
}

.feature {
  background: rgba(0, 255, 0, 0.05);
  padding: 1.5rem;
  border-radius: 8px;
  border: 1px solid rgba(0, 255, 0, 0.1);
  transition: all 0.3s ease;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  user-select: none;
  outline: none;
}

.feature:hover {
  transform: translateY(-5px);
  box-shadow: 0 0 20px rgba(0, 255, 0, 0.2);
  background: rgba(0, 255, 0, 0.1);
}

.feature-icon {
  font-size: 2rem;
  margin-bottom: 1rem;
  display: block;
  height: 2rem;
}

.feature h3 {
  color: var(--matrix-green);
  margin: 0.5rem 0;
  font-size: 1.2rem;
  text-align: center;
  min-height: 1.5rem;
}

.feature p {
  color: var(--text-color);
  font-size: 0.9rem;
  margin: 0;
  opacity: 0.8;
  text-align: center;
  min-height: 2.7rem;
}

.cta-text {
  font-size: 1.2rem;
  color: var(--text-color);
  margin: 2rem 0;
  line-height: 1.6;
}

form {
  background: rgba(0, 255, 0, 0.1);
  padding: 2rem;
  border-radius: 8px;
  border: 1px solid var(--matrix-green);
  width: 100%;
  max-width: 400px;
  margin: 1rem auto;
  box-shadow: 0 0 20px rgba(0, 255, 0, 0.2);
  transition: all 0.3s ease;
  display: flex;
  flex-direction: column;
  align-items: center;
}

form:hover {
  box-shadow: 0 0 30px rgba(0, 255, 0, 0.3);
  transform: translateY(-5px);
}

input, button {
  width: 100%;
  max-width: 280px;
  padding: 12px;
  margin: 10px 0;
  border: 1px solid var(--matrix-green);
  border-radius: 4px;
  background: rgba(0, 0, 0, 0.8);
  color: var(--matrix-green);
  font-family: 'Courier New', monospace;
  font-size: 1rem;
  text-align: center;
  user-select: text;
}

input:focus {
  outline: none;
  box-shadow: 0 0 10px var(--matrix-green);
}

button {
  background-color: rgba(0, 255, 0, 0.2);
  color: var(--matrix-green);
  cursor: pointer;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 2px;
  transition: all 0.3s ease;
}

button:hover {
  background-color: rgba(0, 255, 0, 0.3);
  box-shadow: 0 0 15px var(--matrix-green);
}

.success-message {
  color: var(--text-color);
  margin-top: 20px;
  display: none;
  animation: glow 2s ease-in-out infinite alternate;
  font-weight: 600;
}

.download-link {
  margin-top: 20px;
  display: none;
  color: var(--text-color);
  font-weight: bold;
  text-decoration: none;
  padding: 10px 20px;
  border: 1px solid var(--text-color);
  border-radius: 4px;
  transition: all 0.3s ease;
}

.download-link:hover {
  background: rgba(255, 255, 255, 0.2);
  box-shadow: 0 0 15px rgba(255, 255, 255, 0.5);
}

@keyframes glow {
  from {
    text-shadow: 0 0 2px rgba(0, 255, 0, 0.5),
                0 0 4px rgba(0, 255, 0, 0.3),
                0 0 6px rgba(0, 255, 0, 0.2);
  }
  to {
    text-shadow: 0 0 4px rgba(0, 255, 0, 0.5),
                0 0 8px rgba(0, 255, 0, 0.3),
                0 0 12px rgba(0, 255, 0, 0.2);
  }
}

.methodology {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
  margin: 2rem 0;
  width: 100%;
  max-width: 1200px;
}

.methodology-item {
  background: rgba(0, 255, 0, 0.05);
  padding: 2rem;
  border-radius: 12px;
  border: 1px solid rgba(0, 255, 0, 0.1);
  transition: all 0.3s ease;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  position: relative;
  overflow: hidden;
  user-select: none;
  outline: none;
}

.methodology-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, transparent, rgba(0, 255, 0, 0.05), transparent);
  transform: translateX(-100%);
  transition: transform 0.6s ease;
}

.methodology-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 0 30px rgba(0, 255, 0, 0.2);
  background: rgba(0, 255, 0, 0.1);
}

.methodology-item:hover::before {
  transform: translateX(100%);
}

.methodology-icon {
  font-size: 3rem;
  margin-bottom: 1.5rem;
  display: block;
  height: 3rem;
  filter: drop-shadow(0 0 10px rgba(0, 255, 0, 0.3));
}

.methodology-item h3 {
  color: var(--matrix-green);
  margin: 0.5rem 0;
  font-size: 1.3rem;
  text-align: center;
  min-height: 1.5rem;
  font-weight: 700;
}

.methodology-item p {
  color: var(--text-color);
  font-size: 1rem;
  margin: 0;
  opacity: 0.9;
  text-align: center;
  line-height: 1.6;
}

@media (max-width: 768px) {
  .container {
    padding: 1rem;
    max-width: 100%;
  }

  .content-card {
    padding: 1.5rem;
    width: 100%;
  }

  .features, .methodology {
    grid-template-columns: 1fr;
    max-width: 100%;
    margin: 2rem 0;
  }

  h1 {
    font-size: 2.5rem;
  }

  h2 {
    font-size: 1.5rem;
  }

  .hero-image {
    margin: 1rem 0;
  }
} 